<template>
	<div class="wrap">
		<el-row>
		  <el-col :span="24" style="margin-top: 25vh;">
		  	<div class="grid-content" align="center">
		  		<el-button type="danger"  @click="dialogFormVisible = true">创建项目</el-button>
		  	</div>
		  	<div class="grid-content" align="center">
		  		或者
		  	</div>
		  	<div class="grid-content" align="center">
		  		<el-button type="primary" @click="dialogFormVisibleLogin = true">登录</el-button>
		  	</div>
		  </el-col>
		</el-row>
		
		<el-dialog title="添加项目" :visible.sync="dialogFormVisible" width="90%">
		  <el-form :model="form">
		    <el-form-item>
		      <el-input v-model="form.enterprasename" auto-complete="off" placeholder="企业名称"></el-input>
		    </el-form-item>
		    <el-form-item>
		      <el-input v-model="form.adress" auto-complete="off" placeholder="营业执照号码"></el-input>
		    </el-form-item>
		    <el-form-item>
		      <el-input v-model="form.name" auto-complete="off" placeholder="联系人姓名"></el-input>
		    </el-form-item>
		    <el-form-item>
		      <el-input v-model="form.phone" auto-complete="off" placeholder="联系人手机号"></el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="add">确 定</el-button>
		  </div>
		</el-dialog>
		
		
		<el-dialog title="登录" :visible.sync="dialogFormVisibleLogin" width="90%">
		  <el-form :model="form2">
		    <el-form-item>
		      <el-input v-model="form2.name" auto-complete="off" placeholder="账号"></el-input>
		    </el-form-item>
		    <el-form-item>
		      <el-input v-model="form2.passwd" auto-complete="off" placeholder="密码"></el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisibleLogin = false">取 消</el-button>
		    <el-button type="primary" @click="to">登录</el-button>
		  </div>
		</el-dialog>
		
	</div>
	
	
</template>
<script>
  export default {
    data() {
      return {
      	dialogFormVisible: false,
      	dialogFormVisibleLogin: false,
        form: {
          enterprasename: '',
          adress: '',
          name: '',
          phone: '',
          
        },
        form2: {
	    	passwd: '',
	    	name: '',
        },
        
     }
    },
    methods : {
     	add : function(){
     		var that = this
     		that.$router.push('/besthe')
     	},
     	to : function(){
     		var that = this
     		that.$router.push('/besthe/man')
     	}
     }
  }
</script>

<style>
	.grid-content{
		margin-bottom: 0.5rem;
	}
	.el-row .item{
		margin-bottom: 0.3rem;
	}
</style>



