<template>
	<div class="wrap">
		<el-card class="box-card">
				<div class="text item">
		    	<b>当前位置信息 <el-button type="text" icon="el-icon-location-outline" style="font-size: 0.6rem;"></el-button></b>
		   </div>
			 <div class="text item">
		    	北京市海淀区
		   </div>
		</el-card>

		<el-card class="box-card success">
		   <div class="text item">
		    	<b>企业基本信息</b>
		   </div>
		   
		    <div class="text item" v-for="item in items1">
		    	 {{ item.message }}
		    </div>
		  
		  <div class="text item" style="text-align: center;">
		   		 <el-button @click="addBusInfo">查看</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
		  <div class="text item">
		    	<b>证照信息</b>
		  </div>
		  
		  	<div class="text item" v-for="item in items4">
		    	 {{ item.message }}
		    </div>
		   
		  <div class="text item" style="text-align: center;">
		   		 <el-button type="danger" @click="addCertInfo">完善</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>法人及股东信息</b>
		    </div>
		    <div class="text item" v-for="item in items6">
		    	 {{ item.message }}
		    </div>
		    	<div class="text item" style="text-align: center;margin: 0.3rem auto;">
					<el-button type="danger" @click="addShareHolder">完善</el-button>
				</div>
		</el-card>
		
		<el-card class="box-card">
		  <div class="text item">
		    	<b>相关附件</b>
		  </div>
		  
		  	<div class="text item" v-for="item in items5">
		    	 {{ item.message }}
		    </div>
		   
		  <div class="text item" style="text-align: center;">
		   		 <el-button type="danger" @click="addRelateAcc">完善</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card　success">
		  <div class="text item">
		    	<b>尽职调查问卷</b>
		  </div>
		   
		  <div class="text item" style="text-align: center;">
		   		 <el-button @click="perfect">查看</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
		   <div class="text item">
		    	<b>贷前问卷</b>
		   </div>
		   <p style="color: #cf0812;">
		   	tips:贷前问卷将采用视频聊天的形式进行访问，请确保网络通畅
		   </p>
		  <div class="text item" style="text-align: center;">
		   		 <el-button type="danger" @click="dialogVisible = true">申请开启问卷调查</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
		   <div class="text item">
		    	<b>授权函</b>
		   </div>
		   <p style="color: #cf0812;">
		   	tips:授权函需要法人签字盖章确认（《个人征信授权书》、《尽调授权委托书》、《企业征信授权书》）
		   </p>
		  <div class="text item" style="text-align: center;">
		  		 <el-button type="primary" @click="UpAuthorLetter">上传授权函</el-button>
		   		 <el-button type="danger" @click="previewAuthorLetter">下载授权函</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>法人签字</b>
		    </div>
		    <router-link to="/sign">
		    	<div class="text item" style="text-align: center;margin: 0.3rem auto;">
					<el-button type="danger">完善</el-button>
				</div>
		  	</router-link>
		</el-card>
		
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>法人签字盖章视频</b>
		    </div>
			<!--<div class="text item" style="text-align: center;margin: 0.3rem auto;">
				<el-button type="danger" @click="upVideoBtn">完善</el-button>
				<input style="display: none;" value="完善" type="file" name="vid" id="vid" accept="video/*" ref="videoBox" />
			</div>-->
		   <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>
		<el-card class="box-card">
			<div class="text item">
		    	<b>预留印鉴</b>
		    </div>
			<!--<div class="text item" style="text-align: center;margin: 0.3rem auto;">
				<el-button type="danger" @click="upVideoBtn">完善</el-button>
				<input style="display: none;" value="完善" type="file" name="vid" id="vid" accept="video/*" ref="videoBox" />
			</div>-->
		   <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>
		<el-card class="box-card">
			<div class="text item">
		    	<b>法人合影</b>
		    </div>
			<!--<div class="text item" style="text-align: center;margin: 0.3rem auto;">
				<el-button type="danger" @click="upVideoBtn">完善</el-button>
				<input style="display: none;" value="完善" type="file" name="vid" id="vid" accept="video/*" ref="videoBox" />
			</div>-->
		   <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>


		<el-card class="box-card">
			<div class="text item">
		    	<b>资料确认</b>
		    </div>
				<div class="text item" style="text-align: center;">
		  		 <el-button type="danger" @click="goDataConfirm">完善</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>客户经理意见及补充说明</b>
		    </div>
			<el-input type="textarea"></el-input>
			<div class="text item" style="text-align: center;margin: 0.3rem auto;">
				<el-checkbox  v-model="checked">完整性</el-checkbox>
  				<el-checkbox  v-model="checked">真实性</el-checkbox>
  				<el-checkbox  v-model="checked">有效性</el-checkbox>
			</div>
		</el-card>
		
		<p align="center">
			<el-button type="danger" style="padding: 12px 3rem;">保存</el-button>
		</p>
		
		<el-dialog
		  title="提示"
		  :visible.sync="dialogVisible"
		  width="90%"
		  :before-close="handleClose">
		  <span>您正在申请开启贷前调查，请认真核对填写信息</span>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="goCreditReport">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>
	
<script>
  export default {
    data() {
      return {
      	checked: true,
      	imageUrl: '',
      	dialogVisible: false,
      	items: [
	      { message: '贵公司上下游企业（采购/经销）主要位于哪些地区，主要是几线城市' },
	      { message: '贵公司在当地的融资成本是多少?当地金融环境如何?企业主要融资方式有哪些（金融机构、小贷公司、现金贷、民间拆借等）' },
	      { message: '结合贵公司经验，向银行申请融资遇到的主要困难是什么，什么样的企业在当地容易获得融资' },
	      { message: '贵公司对于应收账款，能接受的账期是多少' },
	      { message: '贵公司的上游供应商主要从事什么行业' },
	      { message: '贵公司的下游经销商主要从事什么行业' },
	      { message: '贵公司的上下游企业对于电子票据的接受程度' },
	      { message: '如果贵公司的交易对手接受商业承兑汇票，可接受的商票贴现（年化）成本大概是多少' },
	      { message: '为更好为您服务，想了解一下贵公司财务部门现状' },
	      { message: '人民金服提供的“信券”产品，可以实现拆分、融资及流转功能；尤其是流转功能可以大幅降低贵企业融资成本。如果您接收到“信券”后，是否重点考虑操作流转功能' },
	    ],
	    items1: [
	      { message: '企业名称' },
				{ message: '营业执照注册号' },
	      { message: '注册时间' },
	      { message: '注册资本' },
	      { message: '注册地址' },
	      { message: '经营地址' },
	      { message: '行业分类' },
	      { message: '经营范围' },
				{ message: '备注' },
	    ],
	    items3: [
	      { message: '企业名称' },
	      { message: '申报机构' },
	    ],
	    items4: [
	      { message: '营业执照' },
	      { message: '开户许可证' },
	      { message: '法人身份证' },
	    ],
	    items5: [
	      { message: '企业章程' },
	      { message: '财务报告' },
	    ],
	    items6: [
	      { message: '法人姓名' },
	      { message: '身份证号' },
	      { message: '联系方式' },
	      { message: '股东名称' },
	      { message: '持股比例' },
	      { message: '股东类型' },
	    ],
     }
    },
    methods : {
     	perfect : function(){
     		var that = this
     		that.$router.push('/besthe/add')
     	},
     	addBusInfo : function(){
     		var that = this
     		that.$router.push('/besthe/basicbusinfo')
     	},
     	addCertInfo : function(){
     		var that = this
     		that.$router.push('/besthe/certinfo')
     	},
     	addRelateAcc : function(){
     		var that = this
     		that.$router.push('/besthe/relateacc')
     	},
     	addShareHolder : function(){
     		var that = this
     		that.$router.push('/besthe/shareholder')
     	},
     	previewAuthorLetter : function(){
     		var that = this
     		that.$router.push('/besthe/authorletter')
			},
			UpAuthorLetter : function(){
     		var that = this
     		that.$router.push('/besthe/upauthorletter')
     	},
     	goCreditReport : function(){
     		var that = this
     		that.$router.push('/besthe/creditreport')
			 },
			 goDataConfirm : function(){
     		var that = this
     		that.$router.push('/besthe/dataconfirm')
     	},
     	upVideoBtn : function(){
     		this.$refs.videoBox.dispatchEvent(new MouseEvent('click'))
     	},
	     handleClose(done) {
	        this.$confirm('确认关闭？')
	          .then(_ => {
	            this.dialogVisible = false
	          })
	          .catch(_ => {});
	      },
     	handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
	      },
	      beforeAvatarUpload(file) {
	        const isLt2M = file.size / 1024 / 1024 < 2;
	        if (!isLt2M) {
	          this.$message.error('上传头像图片大小不能超过 2MB!');
	        }
	        return isLt2M;
	      },
	      handleAvatarError(res, file) {
	        console.log(res)
	        alert("上传失败")
	      },
     }
  }
</script>

<style>
	div{
		margin: 0 auto;
	}
	.box-card{
		margin-top:0.2rem;
	}
	.box-card.success{
		background: #6bdcfe;
		color: white;
	}
	.el-textarea__inner{
		height: 2rem;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-uploader .el-upload{
  	width: 100%;
  }
	.el-checkbox{
		white-space:inherit
	}
	.widthAuto .el-checkbox__label{
		width:90%;
		vertical-align: middle;
	}
</style>


