<template>
	<div class="wrap">
		<el-row style="text-align: center;margin: 0.4rem;">
		  <el-col :span="24">
		  	<div class="grid-content bg-purple-dark">
		  		<a><img src="../../static/img/logo.png" /></a>
		  	</div>
		  </el-col>
		</el-row>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
			<el-row style="height: 1.6rem;">
			  <div class="text item">
			  	<el-form-item prop="userName">
	   				<el-input v-model="ruleForm.userName" placeholder="请输入您的用户名称"></el-input>
	  			</el-form-item>
			  </div>
			</el-row>
		
		<el-row style="height: 1.6rem;">
		  <div class="text item">
		  	<el-form-item prop="userPassword">
   				<el-input type="password" v-model="ruleForm.userPassword" placeholder="请输入您的登录密码"></el-input>
  			</el-form-item>
		  </div>
		</el-row>
		
		<el-row style="text-align: center;">
		  <el-col :span="24">
		  	<div class="grid-content bg-purple-dark">
		  		<el-button type="danger" @click="submitForm('ruleForm')">登录</el-button>
		  		<router-link to="/reg">注册</router-link>
		  	</div>
		  </el-col>
		</el-row>
		
		</el-form>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				ruleForm: {
		          userName: '',
		          userPassword: '',
        		},
				rules: {
		          userName: [
		          	 { required: true, message: '请输入您的用户名', trigger: 'blur' },
		          ],
		          userPassword: [
		            { required: true, message: '请输入您的密码', trigger: 'blur' }
		          ],
	        	},
			}
		},
		methods : {
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		          	this.$fetch('/login.htm',{
	            		userName: this.ruleForm.userName,
	            		userPassword: this.ruleForm.userPassword
		          	})
					  .then(function (response) {
					  	var that = this
					  	if(response.state == 200){
					  		alert("登录成功")
					  		that.$router.push('/usercenter')
					  	}
						
					  })
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		      },
		}
	}
</script>
<style>
	.wrap{
		text-align: center;
	}
	.nav{
		display: none;
	}
	.el-input{
		margin-bottom: 0;
	}
</style>


