	<div class="wrap">
		
		<el-card class="box-card">
			<el-form ref="form" :model="form">
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入企业名称"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-select v-model="form.region" placeholder="请选择企业类型" style="width:100%">
			      <el-option label="供应商" value="1"></el-option>
			      <el-option label="核心企业" value="2"></el-option>
			       <el-option label="经销商" value="3"></el-option>
			    </el-select>
			  </el-form-item>
			  <el-form-item >
			      <el-date-picker type="date" placeholder="请选择注册时间" v-model="form.date1"  style="width:100%"></el-date-picker>
			  </el-form-item>
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入注册资本"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入注册地址"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入经营地址"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入企业邮编"></el-input>
			  </el-form-item>
			  <el-form-item>
