			  </el-form-item>
			  <el-form-item>
			    <el-input v-model="form.name" placeholder="请输入行业分类"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-input type="textarea" v-model="form.desc" placeholder="经营范围"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-input type="textarea" v-model="form.desc" placeholder="行业分类"></el-input>
			  </el-form-item>
			  <el-form-item style="text-align: center;">
			    <el-button type="danger" @click="onSubmit">保存</el-button>
			  </el-form-item>
			</el-form>
		</el-card>
		
		<form>  
    
</form>  
	</div>
</template>
<script>
	export default {
		data () {
			return {
				checked: '',
				tableData: [
					{question: '营业执照是否年检'},
					{question: '组织代码证、税务登记证是否在有效期内'}, 
					{question: '最新公司章程、验资报告是否真实有效'}, 
					{question: '贷款卡查询是否无不良记录'},
					{question: '工商查询是否合法合规'},
					{question: '法院被执行网站查询是否无涉诉、被执行信息'},
					{question: '千里眼查询是否无重大不良信息'},
				],
		        multipleSelection: [],
		        form: {
		          name: '',
		          region: '',
		          date1: '',
		          desc: ''
		        }
	        
	      	};
	    },
		methods : {
			/*bc : function(){
				var that = this
				that.$router.push('/besthe')
			},*/
			indexMethod(index) {
		        return index + 1;
		     },
			toggleSelection(rows) {
		        if (rows) {
		          rows.forEach(row => {
		            this.$refs.multipleTable.toggleRowSelection(row);
		          });
		        } else {
		          this.$refs.multipleTable.clearSelection();
		        }
		      },
		      handleSelectionChange(val) {
		        this.multipleSelection = val;
		      },
		      onSubmit() {
        console.log('submit!');
      }
		}
	}
</script>
<style>
	b{
		font-size: 0.5rem;
		line-height: 0.8rem;
	}
	.title b{
		font-size:initial;
	}
</style>


