<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span>发票管理</span>
		  </div>
		  <div class="text item" style="text-align: center;">
		    <el-button type="danger">未开票</el-button>
		    <el-button type="danger">处理中</el-button>
		    <el-button type="danger">已开票</el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item" style="margin-bottom: 20px;">
		  	资产订单号：
			FF0171123010AT83
			<el-button type="danger" style="float: right;" @click="open3">删除</el-button>
		  </div>
		  <div class="text item" style="margin-bottom: 20px;"> 贸易合同编号:
			000000000001  
		  </div>
			<div class="text item">采购订单号/唯一交易凭证:
			9144030075253462XK  </div>
			<div class="text item">应收账款金额（元）:
			<110,000.00  </div>
			<div class="text item">兑付日期:
			2032-08-18  </div>
			<div class="text item">原始资产付款方:
			武汉加多宝饮料有限公司  
			</div>
		</el-card>
		
		<el-pagination
		  background
		  layout="prev, pager, next"
		  :total="30" class="pages">
		</el-pagination>
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				form: {
		          region: '',
		        }
			}
		},
		methods: {
	      open3() {
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '操作成功!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
</style>


