<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="text item">
		    	<b>人民金服</b>
		  </div>
		  	<div class="text item right-text">
		  		<p>
		  			<label>联系人姓名：</label>{{name}}
		  		</p>
		  		<p>
		  			<label>联系人手机号：</label>{{phone}}
		  		</p>
		  		<p>
		  			<label>地址：</label>{{address}}
		  		</p>
		    	
		    </div>
		    <div class="text item" style="text-align: center;">
		   		 <el-button @click="goBesthe">申请尽调</el-button>
		    </div>
		</el-card>
		<el-card class="box-card success">
		  <div class="text item">
		    	爱奇艺<el-button type="text" style="float:right;padding:0;color: white;">删除</el-button>
		  </div>
		</el-card>
		<el-card class="box-card timeout">
		  <div class="text item">
		   		微众银行<el-button type="text" style="float:right;padding:0;">删除</el-button>
		  </div>
		</el-card>
		
		<el-button icon="el-icon-refresh" style="position: fixed;bottom: 0;left: 0;width: 100%;padding: 0.4rem 0;border-radius: 0;font-size: 0.8rem;"></el-button>
		
	</div>
</template>
	
<script>
  export default {
    data() {
      return {
        name: '张三',
        phone:'123123123',
        address:'北京北京',
     }
    },
    methods: {
    	goBesthe: function(){
    		var that = this
    		that.$router.push('/besthe')
    	}
    }
  }
</script>

<style>
	.el-date-editor.el-input, .el-date-editor.el-input__inner{
		width: 100%;
	}
	.box-card.title{
		margin-top:0rem;
	}
	.box-card{
		margin-top:0.2rem;
	}
	.box-card.timeout{
		background: #ecf1f7;
	}
	.box-card.success{
		background: #6bdcfe;
		color: white;
	}
	.right-text label{
		display: inline-block;
		text-align: right;
		width: 2.7rem;
	}
</style>


