<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="clearfix" align="center">
		    <span>消息中心</span>
		  </div>
		</el-card>
		<div v-for="(news, index) of listData" :index = index>
			<el-card v-if="news.messageStatus == 0" class="box-card box-card2">
					<div class="text item"  style="margin-bottom: 20px;">
			  	{{ news.messageTitle }}
				<el-button type="danger" style="float: right;" @click="open3(news.id)">删除</el-button>
			 	 	</div>
			 	 	<div class="text item" style="margin-bottom: 20px;">{{ news.sendTime }}</div>
			 	 	<div class="text item" style="text-align: center;">
			 	 		<router-link :to="{ name: 'msgdetail', params: { id: news.id }}">查看详情</router-link>
			 	 	</div>
				<div class="text item" style="margin-bottom: 20px;" ref="msg"></div>
			</el-card>
			<el-card v-else class="box-card box-card2" style="background: rgba(0,0,0,.1);">
				<div class="text item"  style="margin-bottom: 20px;">
			  		{{ news.messageTitle }}
					<el-button type="danger" style="float: right;" @click="open3(news.id)">删除</el-button>
				</div>
			 	<div class="text item" style="margin-bottom: 20px;">{{ news.sendTime }}</div>
			 	 	<div class="text item" style="text-align: center;">
			 	 		<router-link :to="{ name: 'msgdetail', params: { id: news.id }}">查看详情</router-link>
			 	 	</div>
				<div class="text item" style="margin-bottom: 20px;" ref="msg"></div>
			</el-card>
		</div>
		<el-pagination
		  background
		  small
		  layout="prev, pager, next"
		  @size-change="handleSizeChange"
		  @current-change="handleCurrentChange"
		  :current-page="currentPage"
          :page-size="pagesize"
          :total="totalNumber" style="margin-top: 1rem;">
		</el-pagination>
	</div>
</template>

<script>
	export default {
		data() {
			return{
				//列表数据
				listData: [],
				//默认每页数量
				pagesize: 10,
				//当前页码
				currentPage: 1,
				//默认数据总数
	            totalNumber: 1,
			}
		},
		methods: {
			//从服务器读取数据
			loadData(currentPage,pagesize){
				var that = this
				var params = new URLSearchParams();
					params.append('currentPageNumber', this.currentPage);
					params.append('pageSize', this.pagesize);
				this.$post('/ws/user/message.htm',params)
					  .then(function (response) {
					  	if(response.state == 200){
					  		that.listData = response.data.list;
					  		console.log(that.listData)
                        	that.totalNumber = response.data.totalNumber;
                        	currentPage ++
                        	if(currentPage <= response.data.currentPageNumber){
                        		return false
                        	}
                        	return currentPage
                        	console.log(currentPage)
					  	}else{
					  		console.log(res)
					  		alert(res)	
					  	}
						
					  })
			},
			//每页显示数据量变更
            handleSizeChange: function(val) {
                this.pagesize = val;
                this.loadData( this.currentPage, this.pagesize);
            },

            //页码变更
            handleCurrentChange: function(val) {
                this.currentPage = val;
                this.loadData( this.currentPage, this.pagesize);
            },        
	      open3(id) {
	      	var that = this
	      	console.log(id)
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	        	that.$post('/ws/user/message/del.htm?id='+id+'')
					  .then(function (response) {
					  	if(response.state == 200){
					        alert('操作成功!')
					        that.loadData(that.currentPage, that.pagesize);
					  	}else{
					  		console.log(response)
					  		alert(response.msg)	
					  	}
						
					  })
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      },
	    },
	    created: function(){
	    	this.loadData(this.currentPage, this.pagesize);
		},
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
</style>


