<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span>应收资产</span>
		    <el-button style="float: right; padding: 3px 0" type="text">作为资产收款方</el-button>
		  </div>
		  <div class="text item">
		    <el-form ref="form" :model="form" label-width="80px">
			    <el-form-item label="确权进度">
				    <el-select v-model="form.region" placeholder="请选择确权进度">
				      <el-option label="确权进度一" value="shanghai"></el-option>
				      <el-option label="确权进度二" value="beijing"></el-option>
				    </el-select>
				</el-form-item>
			</el-form>
		  </div>
		</el-card>
		<el-row>
		  <el-col :span="24" style="text-align: center;">
		  	 <el-button type="danger">查询</el-button>
		  </el-col>
		</el-row>
		<el-card class="box-card box-card2">
		  <div class="text item" style="margin-bottom: 1rem;">
		  	资产订单号：
			FF0171123010AT83
			<el-button type="danger" style="float: right;" @click="open2">通过</el-button>
		  </div>
		  <div class="text item" style="margin-bottom: 1rem;"> 贸易合同编号:
			000000000001  
			<el-button type="danger" style="float: right;" @click="open3">拒绝</el-button>
		  </div>
			<div class="text item">采购订单号/唯一交易凭证:
			9144030075253462XK  </div>
			<div class="text item">应收账款金额（元）:
			<110,000.00  </div>
			<div class="text item">兑付日期:
			2032-08-18  </div>
			<div class="text item">原始资产付款方:
			武汉加多宝饮料有限公司  
			<el-button type="text" style="float: right;" round><router-link to="/assetman/first/edit">上传</router-link></el-button>
			</div>
		</el-card>
		
		<el-pagination
		  background
		  layout="prev, pager, next"
		  :total="30" class="pages">
		</el-pagination>
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				form: {
		          region: '',
		        }
			}
		},
		methods: {
	      open2() {
	        this.$confirm('通过?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '操作成功!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      },
	      open3() {
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '操作成功!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
</style>


