<template>
	<div class="wrap">
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>添加法人及股东信息</b>
		    </div>
		    <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="" class="demo-dynamic">
			  <el-form-item label="法人信息">
			    <el-input v-model="dynamicValidateForm.name" placeholder="请输入法人姓名"></el-input>
			    <el-input v-model="dynamicValidateForm.phone" placeholder="请输入法人手机号"></el-input>
			    <el-input v-model="dynamicValidateForm.cardnumber" placeholder="请输入法人姓身份证号"></el-input>
			  </el-form-item>
			  <el-form-item
			    v-for="(domain, index) in dynamicValidateForm.domains"
			    :label="'添加股东'"
			    :key="domain.key"
			    :prop="'domains.' + index + '.value'"
			    :rules="{
			      required: true, message: '请按规则填写', trigger: 'blur'
			    }"
			  >
			    <el-input v-model="domain.value" placeholder="股东名称"></el-input>
			    <el-input v-model="domain.value2" placeholder="占股比例"></el-input>
			    <el-input v-model="domain.value3" placeholder="股东类型"></el-input>
			    <el-button @click.prevent="removeDomain(domain)">删除</el-button>
			    
			  </el-form-item>
			  <el-form-item>
			    <el-button type="primary" @click="submitForm('dynamicValidateForm')">添加</el-button>
			    <el-button @click="addDomain">添加股东</el-button>
			    <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
			  </el-form-item>
			</el-form>
		</el-card>
		
		<form>  
    
</form>  
	</div>
</template>
<script>
	export default {
		data () {
			return {
		        dynamicValidateForm: {
		          domains: [{
		            value: '',
		            value2: '',
		            value3: '',
		          }],
		          name: '',
		          phone: '',
		          cardnumber: '',
		        }
	      	};
	    },
		methods : {
	      submitForm(formName) {
	        this.$refs[formName].validate((valid) => {
	          if (valid) {
	            alert('submit!');
	            console.log(this.dynamicValidateForm)
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
	      },
	      resetForm(formName) {
	        this.$refs[formName].resetFields();
	      },
	      removeDomain(item) {
	        var index = this.dynamicValidateForm.domains.indexOf(item)
	        if (index !== -1) {
	          this.dynamicValidateForm.domains.splice(index, 1)
	        }
	      },
	      addDomain() {
	        this.dynamicValidateForm.domains.push({
	          value: '',
	          key: Date.now()
	        });
	      }
	    }
	}
</script>
<style>
	b{
		font-size: 0.5rem;
		line-height: 0.8rem;
	}
	.title b{
		font-size:initial;
	}
	.el-form-item__label{
		float: inherit;
	}
</style>


