<template>
	<div class="wrap">
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>个人征信授权书</b>
		    </div>
		     <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="/api/ws/file/upload.htm"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>授权委托书</b>
		    </div>
		     <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>企业征信授权书</b>
		    </div>
		     <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		   </div>
		</el-card>
		
		<p align="center">
			<el-button type="danger" style="padding: 12px 3rem;" @click="bc">保存</el-button>
		</p>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				imageUrl: '',
	      	};
	    },
		methods : {
			bc : function(){
				var that = this
				that.$router.push('/besthe')
			},
			handleAvatarSuccess(res, file) {
				console.log(res)
				if(res.state == 200){
					this.imageUrl = URL.createObjectURL(file.raw);
				}else{
					this.imageUrl = URL.createObjectURL(file.raw);
				}
	      	},
			beforeAvatarUpload(file) {
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
				this.$message.error('上传头像图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarError(res, file) {
				console.log(res)
				alert("上传失败")
			},
		}
	}
</script>
<style>
	b{
		font-size: 0.5rem;
		line-height: 0.8rem;
	}
	.title b{
		font-size:initial;
	}
</style>


