<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="text item">
		  	<div>账号安全设置</div>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="text item">
		  	<div>修改密码</div>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="旧密码"></el-input>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="新密码"></el-input>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="确认新密码"></el-input> 
		  </div>
		  <div class="text item" style="text-align: center;">
		   	<el-button type="danger">
		   		提交
		   	</el-button>
		   </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="text item">
		  	<div>修改绑定手机号</div>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="当前手机号验证码" style="width:58%"></el-input>
		  	<el-button type="danger">
		   		获取验证码
		   	</el-button>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="新手机号"></el-input>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="新手机号验证码" style="width:58%"></el-input>
		  	<el-button type="danger">
		   		获取验证码
		   	</el-button>
		  </div>
		  <div class="text item" style="text-align: center;">
		   	<el-button type="danger">
		   		提交
		   	</el-button>
		   </div>
		</el-card>
		
	</div>
</template>
	
<script>
	export default {
    data() {
      return {
        input: '',
      };
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      handleAvatarError(res, file) {
        console.log(res)
        alert("上传失败")
      },
    }
  }
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-uploader .el-upload{
  	width: 100%;
  }
</style>


