import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Reg from '@/components/Reg'
import UserCenter from '@/components/UserCenter'
import Msg from '@/components/Msg'
import MsgDetail from '@/components/MsgDetail'
import AssetMan from '@/components/assetman/AssetMan'
import AssetManFirst from '@/components/assetman/AssetManFirst'
import AssetManFirstEdit from '@/components/assetman/AssetManFirstEdit'
import DebtMan from '@/components/debtman/DebtMan'
import DebtManFirstAdd from '@/components/debtman/DebtManFirstAdd'
import LetSta from '@/components/letsta/LetSta'
import LetStaDetail from '@/components/letsta/LetStaDetail'
import LetStaFirst from '@/components/letsta/LetStaFirst'
import LetStaFirstEdit from '@/components/letsta/LetStaFirstEdit'
import LetStaFirstEditConfirm from '@/components/letsta/LetStaFirstEditConfirm'
import RealName from '@/components/realname/RealName'
import RealNameUserInfo from '@/components/realname/RealNameUserInfo'
import RealNameEnterpriseInfo from '@/components/realname/RealNameEnterpriseInfo'
import RealNameFace from '@/components/realname/RealNameFace'
import RealNameCheck from '@/components/realname/RealNameCheck'
import RealNamePay from '@/components/realname/RealNamePay'
import SecCenter from '@/components/seccenter/SecCenter'
import SecCenterChange from '@/components/seccenter/SecCenterChange'
import Account from '@/components/seccenter/Account'
import InvoiceMan from '@/components/invoiceman/InvoiceMan'
import InvoiceManFirst from '@/components/invoiceman/InvoiceManFirst'
import Elcacc from '@/components/elcacc/Elcacc'
import Rate from '@/components/rate/Rate'
import BesThe from '@/components/besthe/BesThe'
import BesTheLogin from '@/components/besthe/BesTheLogin'
import BesTheMan from '@/components/besthe/BesTheMan'
import BesTheAdd from '@/components/besthe/BesTheAdd'
import BesTheBasicBusInfo from '@/components/besthe/BesTheBasicBusInfo'
import BesTheCertInfo from '@/components/besthe/BesTheCertInfo'
import BesTheRelateAcc from '@/components/besthe/BesTheRelateAcc'
import BesTheAddShareHolder from '@/components/besthe/BesTheAddShareHolder'
import BesTheAddAuthorLetter from '@/components/besthe/BesTheAddAuthorLetter'
import BesTheCreditReport from '@/components/besthe/BesTheCreditReport'
import BesTheDataConfirm from '@/components/besthe/BesTheDataConfirm'
import BesTheUpauthorletter from '@/components/besthe/BesTheUpauthorletter'

import Canvas from '@/components/Canvas'
import Sign from '@/components/Sign'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    { path: '/', name: 'BesTheLogin', component: BesTheLogin },
    { path: '/reg', name: 'reg', component: Reg },
    { path: '/usercenter', name: '用户中心', component: UserCenter, },
    { path: '/msg', name: '消息中心', component: Msg, },
    { path: '/msg/detail/:id', name: '消息详情', component: MsgDetail, },
    { path: '/assetman', name: '资产管理', component: AssetMan, },
    { path: '/assetman/first', name: 'assetmanfirst', component: AssetManFirst, },
    { path: '/assetman/first/edit', name: 'assetmanfirstedit', component: AssetManFirstEdit, },
    { path: '/debtman', name: 'debtman', component: DebtMan },
    { path: '/debtman/first/add', name: 'debtmanfirstadd', component: DebtManFirstAdd },
    { path: '/letsta', name: 'letsta', component: LetSta },
    { path: '/letsta/detail', name: 'letstadetail', component: LetStaDetail },
    { path: '/letsta/first', name: 'letstafirst', component: LetStaFirst },
    { path: '/letsta/first/edit', name: 'letstafirstedit', component: LetStaFirstEdit },
    { path: '/letsta/first/edit/confirm', name: 'letstafirsteditconfirm', component: LetStaFirstEditConfirm },
    { path: '/realname', name: 'realname', component: RealName },
    { path: '/realname/userinfo', name: 'realnameuserinfo', component: RealNameUserInfo },
    { path: '/realname/enterpriseinfo', name: 'realnameenterpriseinfo', component: RealNameEnterpriseInfo },
    { path: '/realname/face', name: 'realnameface', component: RealNameFace },
    { path: '/realname/check', name: 'realnamecheck', component: RealNameCheck },
    { path: '/realname/pay', name: 'realnamepay', component: RealNamePay },
    { path: '/seccenter', name: 'seccenter', component: SecCenter },
    { path: '/seccenter/change', name: 'seccenterchange', component: SecCenterChange },
    { path: '/account', name: 'account', component: Account },
    { path: '/invoiceman', name: 'invoiceman', component: InvoiceMan },
    { path: '/invoiceman/first', name: 'invoicemanfirst', component: InvoiceManFirst },
    { path: '/elcacc', name: 'elcacc', component: Elcacc },
    { path: '/rate', name: 'rate', component: Rate },
    { path: '/besthe', name: '完善尽调信息', component: BesThe },
    { path: '/besthe/login', name: '项目尽调', component: BesTheLogin },
    { path: '/besthe/man', name: '项目列表', component: BesTheMan },
    { path: '/besthe/add', name: '尽职调查问卷', component: BesTheAdd},
    { path: '/besthe/basicbusinfo', name: '企业基本信息', component: BesTheBasicBusInfo},
    { path: '/besthe/certinfo', name: '证照信息', component: BesTheCertInfo},
    { path: '/besthe/relateacc', name: '相关附件', component: BesTheRelateAcc},
    { path: '/besthe/shareholder', name: '添加法人及股东信息', component: BesTheAddShareHolder},
    { path: '/besthe/authorletter', name: '授权函预览', component: BesTheAddAuthorLetter},
    { path: '/besthe/creditreport', name: '贷前调查', component: BesTheCreditReport},
    { path: '/besthe/dataconfirm', name: '资料确认', component: BesTheDataConfirm},
    { path: '/besthe/upauthorletter', name: '上传授权函', component: BesTheUpauthorletter},
    
    { path: '/canvas', name: 'canvas', component: Canvas},
    { path: '/sign', name: 'sign', component: Sign}
  ]
})



