<template>
  <div>
  	<el-card class="box-card title" v-show="isShow">
		  <div class="text item">
		  	<el-button class="add" icon="el-icon-arrow-left" @click="goBack">
		  		<span>{{userName}}</span>
		  	</el-button>
		  	<b>{{msgTitle}}</b>
		  	<el-button class="return" icon="el-icon-setting" @click="open"></el-button>
		  </div>
		</el-card>
		
	    <div style="margin-top:10px">
	      <router-view></router-view>
	    </div>
    
    <el-dialog title="登录" :visible.sync="dialogFormVisibleLogin" width="90%">
		  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
		    <el-form-item prop="userName">
	   				<el-input v-model="ruleForm.userName" placeholder="请输入您的用户名称"></el-input>
	  			</el-form-item>
		    <el-form-item prop="userPassword">
   				<el-input type="password" v-model="ruleForm.userPassword" placeholder="请输入您的登录密码"></el-input>
  			</el-form-item>
		  <div style="text-align: right;">
		    <el-button @click="dialogFormVisibleLogin = false">取 消</el-button>
		    <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
		  </div>
		  </el-form>
		</el-dialog>
  </div>
</template>
<script>
export default {
	userName:'',
	data(){
		return{
			userName: '',  
			dialogVisible: false,
			isShow:true,
			dialogFormVisibleLogin: false,
			msgTitle:'人民金服',
			ruleForm: {
	    	userPassword: '',
	    	userName: '',
     },
     rules: {
          userName: [
          	 { required: true, message: '请输入您的用户名', trigger: 'blur' },
          ],
          userPassword: [
            { required: true, message: '请输入您的密码', trigger: 'blur' }
          ],
    	},
		}
	},
	watch:{
    $route(){
    	var routeName = this.$route.name
    	if(routeName == "reg" || routeName == "login"){
    		this.isShow = false
    	}else{
    		this.msgTitle = this.$route.name
    		this.isShow = true
    	}
    }
  },
	/*created: function(){
	    this.getUserInfo()            
	},*/
	methods: {
		submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
          	this.$fetch('/login.htm',{
        		userName: this.ruleForm.userName,
        		userPassword: this.ruleForm.userPassword
          	})
			  .then(function (response) {
			  	var that = this
			  	if(response.state == 200){
			  		alert("登录成功")
						//that.$router.push('/usercenter')
						that.isShow = false
			  	}
				
			  })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
		getUserInfo(){
			 this.$fetch('/ws/user/info.htm')
	      .then((response) => {
	        this.userName = response.userName
	      })
		},
		loginOut() {
        this.$confirm('确定退出?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        	var that = this
        	that.axios.get('/ws/user/logout.htm')
					  .then(function (res) {
					  	if(res.data.state == 200){
					  		that.$message({
			            type: 'success',
			            message: '退出成功!'
			          });
					  		that.$router.push('/')
					  	}else{
					  		alert("退出失败")	
					  	}
					  })
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      },
      open : function(){
      	//this.isShow = true //!this.isShow;
      	this.dialogFormVisibleLogin = true
      },
      goBack:function(){
      	this.$router.go(-1)
      }
     
	}
}
</script>
<style>

</style>



