<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span>核心操作校验方式</span>
		  </div>
		  <div class="text item" style="text-align: center;">
		  	<el-button type="danger">
		  		短信
			</el-button>  
			<el-button type="danger">
		  		UKEY
			</el-button>  
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item">
		  	经办用户名：
		  </div>
		  <div class="text item">
		  	经办校验手机号：
		  </div>
		  <div class="text item">
		  	经办用户名：
		  </div>
		  <div class="text item">
		  	经办校验手机号：
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item" style="text-align: center; color: #cf0812;">
		  	非电脑客户端暂不支持UEKY认证
您也可以复制以下链接至电脑客户端进行验证
		  </div>
		  <div class="text item">
		  	<el-input v-model="input"></el-input>
		  </div>
		  <div class="text item" style="text-align: center; color: #cf0812;">
		  	或者，登录电脑客户端执行验证操作
		  </div>
		</el-card>
		
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				input: 'http://127.0.0.1/businfo/views/realName/validMsg/coreWay.html'
			}
		},
		methods: {
	      open3() {
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '操作成功!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.check-btn{
		border-radius: 60px;
    	padding: 8px;
    	float:right;
	}
	.transfer-btn{
		position: fixed;
		top: 45%;
		left: 0;
	}
	.el-badge__content.is-fixed{
		top: 4px;
		right: 16px;
		border: none;
	}
</style>


