<template>
	<div class="wrap">
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>企业章程</b>
		    </div>
		     <div class="text item">
		  	<el-upload
			action="/api/ws/file/upload.htm"
			list-type="picture-card"
			:file-list="fileList2"
			:on-success="handleAvatarSuccess"
			:on-error="handleAvatarError"
			:on-preview="handlePreview"
			:on-remove="handleRemove">
			<i class="el-icon-plus"></i>
			</el-upload>
		   </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>财务报告</b>
		    </div>
		     <div class="text item">
		  	<el-upload
			action="/api/ws/file/upload.htm"
			list-type="picture-card"
			:file-list="fileList2"
			:on-success="handleAvatarSuccess"
			:on-error="handleAvatarError"
			:on-preview="handlePreview"
			:on-remove="handleRemove">
			<i class="el-icon-plus"></i>
			</el-upload>
		   </div>
		</el-card>
		
		<el-card class="box-card">
			<div class="text item">
		    	<b>其他</b>
		    	<el-input v-model="input" placeholder="其他资质文件"></el-input>
		    </div>
		     <div class="text item">
		  	<el-upload
			action="/api/ws/file/upload.htm"
			list-type="picture-card"
			:file-list="fileList2"
			:on-success="handleAvatarSuccess"
			:on-error="handleAvatarError"
			:on-preview="handlePreview"
			:on-remove="handleRemove">
			<i class="el-icon-plus"></i>
			</el-upload>
		   </div>
		</el-card>
		<el-dialog :visible.sync="dialogVisible" size="tiny" width="90%">
		<img width="100%" :src="dialogImageUrl" alt="">
		</el-dialog>
		<p align="center">
			<el-button type="danger" style="padding: 12px 3rem;" @click="bc">保存</el-button>
		</p>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				imageUrl: '',
				input: '',
				dialogImageUrl: '',
        		dialogVisible: false,
				fileList2: [
					{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, 
					{name: 'food2.jpeg', url: 'http://pic26.photophoto.cn/20130323/0005018467298586_b.jpg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
				]
	        
	      	};
	    },
		methods : {
			bc : function(){
				var that = this
				that.$router.push('/besthe')
			},
			handleAvatarSuccess(res, file) {
				console.log(res)
        		if(res.state == 200){
					var imgList = {}
					imgList.name = res.data;
					imgList.url = 'http://123.207.141.96:8002'+res.data+'';
					this.fileList2.push(imgList)
					this.imageUrl = res.data;
				}else{
					this.imageUrl = URL.createObjectURL(file.raw);
				}
				console.log(this.fileList2)
	      	},
			beforeAvatarUpload(file) {
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
				this.$message.error('上传头像图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarError(res, file) {
				console.log(res)
				alert("上传失败")
			},
		    handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
				this.dialogImageUrl = file.url;
       			this.dialogVisible = true;
			}
		}
	}
</script>
<style>
	b{
		font-size: 0.5rem;
		line-height: 0.8rem;
	}
	.title b{
		font-size:initial;
	}
	.upload-fj .el-upload{
		height: 1rem;
		line-height: 1rem;
		border: 0;
	}
	.el-upload-list--picture-card .el-upload-list__item{
		width: 140px;
		height: 140px;
	}
	.el-upload--picture-card{
		width: 140px;
		height: 140px;
	}
</style>


