<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="clearfix">
		    <span>刷脸验证</span>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="clearfix">
		    <span>关注人民金服官方公众号（已关注请忽略）</span>
		  </div>
		  <div class="text item" style="text-align: center;">
		  	<img src="../../../static/img/code_03.jpg" width="200px" />
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="clearfix">
		    <span>扫描二维码（请使用微信扫描二维码</span>
		  </div>
		  <div class="text item" style="text-align: center;">
		  	<img src="../../../static/img/code_03.jpg" width="200px" />
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="clearfix">
		    <span>面部识别</span>
		  </div>
		  <div class="text item" style="text-align: center; color: #cf0812;">
		  	请在微信公众号内进行面部识别
		  </div>
		  <div class="text item" style="text-align: center; color: #cf0812;">
		  	<el-checkbox v-model="checked">
		  		《协议》
		  	</el-checkbox>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div slot="header" class="clearfix">
		    <span>等待审核</span>
		  </div>
		  <div class="text item" style="text-align: center; color: #cf0812;">
		  	您的面部影像已提交系统，预计需要10分钟（最长24小时），请耐心等待人工审核，审核结果将以短信形式发送至您注册时绑定的手机号码，请注意查收。
		  </div>
		</el-card>
		
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				checked: '',
			}
		},
		methods: {
	      open3() {
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '操作成功!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.check-btn{
		border-radius: 60px;
    	padding: 8px;
    	float:right;
	}
	.transfer-btn{
		position: fixed;
		top: 45%;
		left: 0;
	}
	.el-badge__content.is-fixed{
		top: 4px;
		right: 16px;
		border: none;
	}
</style>


