<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="clearfix" align="center">
		    <span>消息详情</span>
		  </div>
		</el-card>
			<el-card class="box-card box-card2">
					<div class="text item"  style="margin-bottom: 20px;">
			  			{{ messageTitle }}
						<el-button type="danger" style="float: right;" @click="delet()">删除</el-button>
			 	 	</div>
			 	 	<div class="text item" style="margin-bottom: 20px;">{{ sendTime }}</div>
				<div class="text item" style="margin-bottom: 20px;">
					{{ messageContent }}
				</div>
			</el-card>
		
	</div>
</template>

<script>
	export default {
		data() {
			return{
				messageTitle: '',
				sendTime: '',
				messageContent: '',
				id:'',
			}
		},
		methods: {
	      getNewsDetail(id){
	      	var that = this
	      	that.$fetch('/ws/user/messageDetail.htm',{
	      			'id':id 
	      	})
			  .then(function (response) {
			  	if(response.state == 200){
			  		that.messageTitle = response.data.messageTitle
			  		that.sendTime = response.data.sendTime
			  		that.messageContent = response.data.messageContent
			  	}else{
			  		console.log(response)
			  		alert(response.msg)	
			  	}
				
			  })
	     },
	     delet() {
	      	var that = this
	      	var id = that.$route.params.id
	      	/*var params = new URLSearchParams();
				params.append('id', id);
				console.log(params)*/
	        this.$confirm('删除?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	        	that.$post('/ws/user/message/del.htm?id='+id+'')
					  .then(function (response) {
					  	if(response.state == 200){
					        alert('操作成功!')
					        that.$router.push('/msg')
					  	}else{
					  		console.log(response)
					  		alert(response.msg)	
					  	}
						
					  })
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: '已取消操作'
	          });          
	        });
	      },
	    },
	    created: function(){
	    	this.getNewsDetail(this.$route.params.id);       
		},
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
</style>


