<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="text item">
		  	<div>用户信息</div>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item">
		  	<el-input v-model="input" placeholder="法定代表人名称"></el-input>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="法定代表人手机号"></el-input>
		  </div>
		  <div class="text item">
		  	<el-input v-model="input" placeholder="法定代表人身份证号" style="width:80%"></el-input> 
		  	<el-checkbox v-model="checked">长期</el-checkbox>
		  </div>
		   <div class="text item">
		   	法人身份证正面
		   </div>
		  <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		  </div>
		  <div class="text item">
		   	法人身份证反面
		   </div>
		  <div class="text item">
		  	<el-upload
			  class="avatar-uploader"
			  action="127.0.0.1"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :on-error="handleAvatarError"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon">
			  </i>
			</el-upload>
		  </div>
		</el-card>
		
	</div>
</template>
	
<script>
	export default {
    data() {
      return {
        imageUrl: '',
        input: '',
        checked: '',
      };
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      handleAvatarError(res, file) {
        console.log(res)
        alert("上传失败")
      },
    }
  }
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-uploader .el-upload{
  	width: 100%;
  }
</style>


