<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="text item">
		  	<div>评级信息</div>
		  </div>
		</el-card>
		 <div class="text item" style="margin-top: 0.5rem;font-size: 0.3rem;">
		  	<b>法定代表人基本信息</b>
		  </div>
		<el-form ref="form" :model="form" label-width="auto" class="form">
		  <el-form-item label="文化程度：">
		    <el-input v-model="form.name"></el-input>
		  </el-form-item>
		  <el-form-item label="年可支配收入">
		    <el-input v-model="form.name1"></el-input>
		  </el-form-item>
		  <el-form-item label="户籍所在地：">
		    <el-input v-model="form.name2"></el-input>
		  </el-form-item>
		  <el-form-item label="微信号：">
		    <el-input v-model="form.name4"></el-input>
		  </el-form-item>
		 <div class="text item" style="margin-top: 0.5rem;font-size: 0.3rem;">
		  	<b>企业基本信息</b>
		  </div>
		  <el-form-item label="所属行业：">
		    <el-input v-model="form.name5"></el-input>
		  </el-form-item>
		   <el-form-item label="企业类型：">
		    <el-input v-model="form.name6"></el-input>
		  </el-form-item>
		  <el-form-item label="人员规模：">
		    <el-input v-model="form.name7"></el-input>
		  </el-form-item>
		  <el-form-item label="营业范围">
		    <el-input type="textarea" v-model="form.desc"></el-input>
		  </el-form-item>
		  <el-form-item style="text-align: center;">
		    <el-button type="danger" @click="onSubmit">提交</el-button>
		  </el-form-item>
		</el-form>
		
	</div>
</template>
	
<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          name1: '',
          name2: '',
          name4: '',
          name5: '',
          name6: '',
          name7: '',
          desc: '',
        },
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style>
	.item{
		text-align: left;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	div{
		margin: 0 auto;
	}
	.el-date-editor.el-input, .el-date-editor.el-input__inner{
		width: 100%;
	}
</style>


