<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span>应收资产</span>
		    <el-button style="float: right; padding: 3px 0" type="text">详情/凭证上传</el-button>
		  </div>
		  <div class="text item">
		  	<div>确权信息</div>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item">
		  	资产订单号：
			FF0171123010AT83
		  </div>
		  <div class="text item"> 贸易合同编号:
			000000000001  </div>
			<div class="text item">采购订单号/唯一交易凭证:
			9144030075253462XK  </div>
			<div class="text item">应收账款金额（元）:
			<110,000.00  </div>
			<div class="text item">兑付日期:
			2032-08-18  </div>
			<div class="text item">原始资产付款方:
			武汉加多宝饮料有限公司  </div>
		</el-card>
		
		<el-card class="box-card">
		  <div class="text item">
		  	<div>合同</div>
		  </div>
		</el-card>
		
		<el-upload
		  class="avatar-uploader"
		  action="127.0.0.1"
		  :show-file-list="false"
		  :on-success="handleAvatarSuccess"
		  :on-error="handleAvatarError"
		  :before-upload="beforeAvatarUpload">
		  <img v-if="imageUrl" :src="imageUrl" class="avatar">
		  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
		</el-upload>
		
	</div>
	
</template>

<script>
	export default {
    data() {
      return {
        imageUrl: ''
      };
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      handleAvatarError(res, file) {
        console.log(res)
        alert("上传失败")
      },
    }
  }
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    width: 100%;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


