<template>
	<div class="wrap" style="text-align: left;">
		<el-row style="margin: 0.4rem;">
		  <el-col :span="24">
		  	<div class="grid-content bg-purple-dark" style="text-align: center;"> 
		  		<a><img src="../../static/img/logo.png" /></a>
		  	</div>
		  </el-col>
		</el-row>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
		<el-row>
		  <div class="text item">
		  	<el-form-item prop="enterpriseName">
   				<el-input v-model="ruleForm.enterpriseName" placeholder="请输入您的企业名称"></el-input>
  			</el-form-item>
		  </div>
		  <div class="text item">
		  	<el-form-item prop="userName">
   				<el-input v-model="ruleForm.userName" placeholder="请输入您的用户名称"></el-input>
  			</el-form-item>
		  </div>
		  <div class="text item">
		  	<el-form-item prop="mobile">
   				<el-input v-model="ruleForm.mobile" placeholder="请输入您的手机号"></el-input>
  			</el-form-item>
		  </div>
		  <div class="text item">
		  	<el-form-item prop="imgCode" style="width:45%;display: inline-block;">
   				<el-input v-model="ruleForm.imgCode" placeholder="请输入图片验证码"></el-input>
  			</el-form-item>
		  	<span class="imgwrap">
		  		<img src="../../static/img/img-1.png" />
		  	</span>
		  	<el-button icon="el-icon-refresh" style="padding: 12px;">刷新</el-button>
		  </div>
		  
		  <div class="text item">
		  	<el-form-item prop="msgCode" style="width:56%;display: inline-block;">
   				<el-input v-model="ruleForm.msgCode" placeholder="请输入您收到的短信验证码"></el-input>
  			</el-form-item>
		  	<el-button type="danger">获取短信验证码</el-button>
		  </div>
		  
		  <div class="text item">
		  	<el-form-item prop="userPassword">
   				<el-input type="password" v-model="ruleForm.userPassword" placeholder="请输入您的登录密码"></el-input>
  			</el-form-item>
		  </div>
		  <div class="text item">
		  	<el-form-item prop="confirmPassword">
   				<el-input type="password" v-model="ruleForm.confirmPassword" placeholder="确认您输入的密码"></el-input>
  			</el-form-item>
		  </div>
		  
		  <div class="text item">
		  	<el-form-item prop="invitationCodeFrom">
   				<el-input v-model="ruleForm.invitationCodeFrom" placeholder="请输入您的推荐码，没有推荐码请致电客服"></el-input>
  			</el-form-item>
		  </div>
		  
		  <div class="text item" style="text-align: center;">
		  	 <el-form-item prop="type" style="width: 20px;display: inline-block;">
			    <el-checkbox-group v-model="ruleForm.type">
			      <el-checkbox  name="type"></el-checkbox>
			    </el-checkbox-group>
			  </el-form-item>我已同意并阅读《人民金服用户注册协议》
		  </div>
		  <div class="text item" style="text-align: center;">
		  	  <el-form-item>
			    <el-button type="danger" @click="submitForm('ruleForm')">注册</el-button>
			    <router-link to="/">登录</router-link>
			  </el-form-item>
		  </div>
		</el-row>
		</el-form>
	</div>
</template>
<script>
	export default {
		data () {
			 var validatePass = (rule, value, callback) => {
		        if (value === '') {
		          callback(new Error('请输入密码'));
		        } else {
		          if (this.ruleForm.confirmPassword !== '') {
		            this.$refs.ruleForm.validateField('confirmPassword');
		          }
		          callback();
		        }
		      };
			var validatePass2 = (rule, value, callback) => {
		        if (value === '') {
		          callback(new Error('请再次输入密码'));
		        } else if (value !== this.ruleForm.userPassword) {
		          callback(new Error('两次输入密码不一致!'));
		        } else {
		          callback();
		        }
		      };
			return {
				ruleForm: {
		          enterpriseName: '',
		          userName: '',
		          mobile: '',
		          imgCode: '',
		          msgCode: '',
		          userPassword: '',
		          confirmPassword: '',
		          invitationCodeFrom: '',
		          type: [],
        		},
				rules: {
		          enterpriseName: [
		          	 { required: true, message: '请输入您的企业名称', trigger: 'blur' },
		          ],
		          userName: [
		          	 { required: true, message: '请输入您的企业名称', trigger: 'blur' },
		          ],
		          mobile: [
		            { required: true, message: '请输入您的手机号', trigger: 'blur' },
		            { min: 11, max: 11, message: '长度为11个字符', trigger: 'blur' }
		          ],
		          imgCode: [
		            { required: true, message: '请输入图片验证码', trigger: 'blur' }
		          ],
		          msgCode: [
		            { required: true, message: '请输入短信验证码', trigger: 'blur' }
		          ],
		          userPassword: [
		            { validator: validatePass, trigger: 'blur' }
		          ],
		          confirmPassword: [
		            { validator: validatePass2, trigger: 'blur' }
		          ],
		          invitationCodeFrom: [
		          	 { required: true, message: '请输入您的邀请码', trigger: 'blur' },
		          ],
		          type: [
		            { type: 'array', required: true, message: '请同意并阅读协议', trigger: 'change' }
		          ],
		          
	        	},
	        
	      	};
	    },
		methods : {
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		            alert('submit!');
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		      },
		}
	}
</script>
<style>
	.imgwrap{
		display: inline-block;
		height: 40px;
		vertical-align: middle;
		margin-top: -5px;
	}
	.imgwrap img{
		height: 40px;
	}
	.nav{
		display: none;
	}
	div.item{
		margin-bottom: 0.4rem;
	}
	div.el-form-item__error{
		width: 120px;
	}
</style>


