<template>
	<div class="wrap">
		<el-card class="box-card0">
		  <div slot="header" class="clearfix">
		    <span>选择用户</span>
		  </div>
		  <div class="changeUser">
		  	<div v-for="o in 2" :key="o" class="text item" style="text-align: left;">
		    	{{'户名 ' + o }} <el-button style="float: right;" @click="setDefault(o)">切换</el-button>
		 	 </div>
		  </div>
		</el-card>
		<el-badge :value="4" class="item" style="display: inherit;margin:0.3rem auto;width: 16%;text-align: center;">
		  <el-button size="small">法人</el-button>
		</el-badge>
		
		<el-card class="box-card">
		<el-collapse accordion class="nav-list">
		  <el-collapse-item>
		    <template slot="title">
		      	信用金
		    </template>
		    <div><router-link to="assetman">资产管理</router-link></div>
			<div><router-link to="debtman">债务管理</router-link></div>
			<div><router-link to="letsta">我的信券</router-link></div>
			<div><router-link to="elcacc">电子账户管理</router-link></div>
		  </el-collapse-item>
		  <el-collapse-item>
		    <template slot="title">
		      	贴票
		    </template>
		    <div><router-link to="/seccenter">安全中心</router-link></div>
			<div><router-link to="/realname">实名认证</router-link></div>
			<div><router-link to="/rate">评级信息</router-link></div>
			<div><router-link to="/account">账户管理</router-link></div>
			<div><router-link to="/msg">消息中心</router-link></div>
			<div><router-link to="/invoiceman">发票管理</router-link></div>
			<div><router-link to="/besthe/login">申请尽调</router-link></div>
		  </el-collapse-item>
		</el-collapse>
		</el-card>
		
	</div>
</template>
<script>
	export default {
		data () {
			return {
				titleMsg:'用户中心'
			}
		},
		methods : {
			setDefault: function (o) {
				console.log(o)
	        },
		}
	}
</script>

<style>
	.wrap{
		text-align: left;
	}
  .text {
    font-size: 14px;
  }

  .box-card0 .item {
    margin-bottom: 1.0rem;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .nav-list div{
  	margin-bottom: 0;
  }
  .nav{
		display: block;
	}
</style>


